.-navigation_drawer_button_{
    --navigation-drawer-collapse-transition-padding: padding 250ms cubic-bezier(0.0, 0.0, 0.2, 1) 167ms;
    -webkit-transition: var(--navigation-drawer-collapse-transition-padding);
    transition: var(--navigation-drawer-collapse-transition-padding);   
}

.-navigation_drawer_button_.menu_restaurant{
    display: block;
    margin: 20px 5px;
    -webkit-transition: margin 250ms cubic-bezier(0.0, 0.0, 0.2, 1) 167ms;
    transition: margin 250ms cubic-bezier(0.0, 0.0, 0.2, 1) 167ms;
}

.ytcp-navigation-drawer .imageOnMenu img{
    height: 120px;
    width: 120px;
    margin: 5px auto;
    border-radius: 20%;
    /*
    --navigation-drawer-collapse-transition: width 250ms cubic-bezier(0.0, 0.0, 0.2, 1) 167ms, height 250ms cubic-bezier(0.0, 0.0, 0.2, 1) 167ms;
    transition: var(--navigation-drawer-collapse-transition);  
    position: absolute; 
    */
    --icon-height: 24px;
    --icon-width: 24px;
    border-radius: 50%;
    background-color: transparent;
    overflow: hidden;
    --thumbnail-top-padding:24px;
    --thumbnail-collapse-transition: 300ms cubic-bezier(0.0, 0.0, 0.2, 1) 167ms;
    
    top: var(--thumbnail-top-padding);
    left: calc((var(--navigation-drawer-expanded-width) - 112px) / 2);
    height: 112px;
    width: 112px;   
    opacity: 1;
    -webkit-transition: all var(--thumbnail-collapse-transition);
    transition: all var(--thumbnail-collapse-transition);
    -webkit-transition-property: height, left, padding, top, width;
    transition-property: height, left, padding, top, width;
}



[collapsed-nav="true"] .ytcp-navigation-drawer .imageOnMenu img {
    width: 60px;
    height: 60px;
    height: 32px;
    left: calc((var(--navigation-drawer-collapsed-width) - 32px) / 2);
    top: 16px;
    width: 32px;
}
  
[collapsed-nav="true"] .-navigation_drawer_button_ {
