.month-selector_{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}


.month-selector__range__icons, .month-selector__range__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 48px;
    padding: 8px;
    background: #fff;
    border: 1px solid #DADCE0;
    border-radius: 100px;
}



.month-selector__range__icons {
    display: none;
    position: absolute;
    border: 0 none;
    padding-top: 0;
    padding-bottom: 0;
    bottom: 48px;
    -webkit-align-items: flex-end;
    -ms-flex-align: end;
    -ms-grid-row-align: flex-end;
    align-items: flex-end;


    opacity: 0;
}



.month-selector__range__icons__icon {
    width: 88px;
    height: 44px;
    opacity: 0;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}



.month-selector__range__icons__icon--5 {
    background-image: url(https://fi.google.com/about/static/images/plan/illo_slider_5.png);
}


.month-selector__range__icons__icon--1 {
    background-image: url(https://fi.google.com/about/static/images/plan/illo_slider_1.png);
}




.month-selector__range [data-month-selector-active-index="2"] .month-selector__range__icons__icon--2 {
    opacity: 1.0;
    -webkit-transform: translateY(10px);
    transform: translateY(10px);
}





.month-selector__range__list__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    height: 32px;
}


.month-selector__range {
    /* 
    max-width: 500px; 
    margin: 16px auto 32px;
    */
    margin: 16px auto 2px;
}


.month-selector__range__list__item.active {
    color: #fff;
    background: #1A73E8;
    box-shadow: 0px 2px 6px rgba(60, 64, 67, 0.15), 0px 1px 2px rgba(60, 64, 67, 0.3);
    border-radius: 100px;
    padding: 0 0 4px 0px;
}


.month-selector_year{
    background: var(--calendar--back--color--);
    color: var(--color-base--hover);
    box-shadow: 0px 2px 6px rgba(60, 64, 67, 0.15), 0px 1px 2px rgba(60, 64, 67, 0.3);
    position: relative;
    font: 500 13px/17px Roboto,"Roboto",sans-serif;    
}

.month-selector_year.active{
    color: #fff;
    background: #1A73E8;
}

.month-selector_year.left {
    padding: 10px 15px 10px 10px;
    border-bottom-left-radius: 50px;
    border-top-left-radius: 50px;
    left: 12px;
}


.month-selector_year.right {
    padding: 10px 15px 10px 10px;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    right: 12px;
}


.month-selector__range__list {
    z-index: 25;
}

@media (min-width: 900px){
    .month-selector__range {
        position: relative;
       /* margin: 60px auto 56px;*/
    }
    .month-selector__range__icons {
        display: flex;
