    width: 100%;
    height: 100%;
    cursor: pointer;
    position: absolute;
    overflow: hidden;
    z-index: 1;
  }

  .rippleHRM_effect {
    width: 10px;
    height: 10px;
    background: #ffffff;
    opacity:0.2;
    border-radius: 50%;
    position: absolute;
    top: 10px;
    left: 10px;
    z-index:0;
    -webkit-transition: all 3s ease;
    transition: all 3s ease;
  }

  
  .rippleHRM_close {
    -webkit-animation: _ripple3 1.5s normal forwards cubic-bezier(0,0,0,1);
            animation: _ripple3 1.5s normal forwards cubic-bezier(0,0,0,1);
  }



  @-webkit-keyframes _ripple3 {
    0%{
      -webkit-transform:scale(1);
              transform:scale(1);
    }
    100%{
      -webkit-transform:scale(5);
              transform:scale(5);
      opacity:0;
    }
  }



  @keyframes _ripple3 {
    0%{
      -webkit-transform:scale(1);
              transform:scale(1);
    }
    100%{
      -webkit-transform:scale(5);
              transform:scale(5);
      opacity:0;
    }
  }
